
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { getWordsList } from '@/api/words'

export default {
name: 'Words',
directives: { TransferDom },
components: { InlineLoading, XDialog },
data () {
return {
height: 0,
showLoading: true,
showDialogStyle: false,
list: [],
articleData: {}
}
},
created () {
this.height = document.body.offsetHeight - 120 + 'px'
console.log(document.body.offsetHeight)
this.getWordsList()
},
methods: {
checkWords (item) {
this.showDialogStyle = true
this.articleData = item
},
getWordsList () {
this.showLoading = true
getWordsList().then(res => {
this.showLoading = false
this.list = res.data
}).catch(res => {
this.showLoading = false
})
}
}
}
